import React, { Component } from 'react';
import { Image, FlatList, View, Text, SafeAreaView, Pressable, StatusBar, ActivityIndicator, Dimensions, ImageBackground } from 'react-native';
import { capitalize } from './subcomponents/EditEmotions';

const unsplash_APIKEY = '5fRBt7AhEBCWmlgXrHd-k6RYsEq0t4aJGy5kxkCofDQ' 
const unsplashUrl = 'https://api.unsplash.com/'

const window = Dimensions.get('window')
const windowWidth = window.width;
const windowHeight = window.height;
const screenHeight = windowHeight + StatusBar.currentHeight

function formatTopicName(name){
  return capitalize(name.split('-').join(' '))
}

const styles = {
  header: {
    position: 'absolute',
    top: 0,
    paddingTop: StatusBar.currentHeight,
    width: windowWidth,
    height: '20%',
    backgroundColor: 'rgba(255,255,255,0.5)',
    justifyContent: 'space-evenly',
    alignItems: 'center'
  },
  h1: {
    fontSize: 24,
    alignSelf: 'center',
    // borderWidth: 1
  },
  headerLabel: {
    // borderWidth: 1,
    width: 200,
    fontSize: 20,
    alignSelf: 'center',
    textAlign: 'center',
  },
  status: {
    backgroundColor: 'rgb(230,230,230)',
    width: '100%',
    height: '20%',
    borderRadius: 20,
    alignItems: 'center',
    justifyContent: 'center'
  },
  statusText: {
    fontSize: 18,
    color: 'black'
  },
  photosView: {
    height: screenHeight,
  },
  imagePressable: {
    width: windowWidth/2,
    height: (screenHeight)/2,
  },
  image: {
    width: '100%',
    height: '100%',
    alignItems: 'center',
    justifyContent: 'center'
  },
}

const Item = (props) => {
  const topicsScreen = props.route.name == 'WallpaperTopics'
  const nextScreenName = topicsScreen ? 'Wallpapers' : 'WallpaperZoom'
  const photoURI = topicsScreen ? props.item.cover_photo.urls.regular : props.item.urls.regular
  return(
    <Pressable
      onPress={() => props.navigation.navigate( nextScreenName , { selectedImage: props.item })}
      style={[styles.imagePressable, {height: props.route.params.photoHeight}]}
    >
      <ImageBackground source={{ uri:  photoURI }} style={styles.image} >
        <Text style={[styles.h1, {width: '70%', color: 'white', textAlign: 'center'}]}>{ topicsScreen ? formatTopicName(props.item.slug) : '' }</Text>
      </ImageBackground>
    </Pressable>
  )
};

async function getUnsplash(unsplashParams) {

  console.log('UNSPLASH GET PHOTOS STATUS: STARTED...')
  const { endpoint, page, per_page, order_by } = unsplashParams
  const reqParams = {
    method: 'GET',
    headers: {
      // 'Accept-Version': 'v1',
      // 'Authorization': `Client-ID ${unsplash_APIKEY}`
    }
  }
  const url = `${unsplashUrl + endpoint}?page=${page}&per_page=${per_page}&order_by=${order_by}&client_id=${unsplash_APIKEY}`
  var result, result_json, success
  try {
    result = await fetch(url, reqParams)
    if (result.ok) {

      console.log(`UNSPLASH PHOTOS STATUS: FETCH SUCCESSFULL...`)
      console.log(`UNSPLASH PHOTOS STATUS:\n  x-ratelimit-limit: ${result.headers.map['x-ratelimit-limit']}\n  x-ratelimit-remaining: ${result.headers.map['x-ratelimit-remaining']}`)
      result_json = await result.json()
      success = result_json.length
      if (success) {
        console.log(`UNSPLASH PHOTOS STATUS: JSON RESULT SUCCESSFULL. RETRIEVED ${success} PHOTOS...`)

      } else {
        console.log(`UNSPLASH PHOTOS STATUS: JSON RESULT FAILED. Logging json value...`)
        console.log(result_json)
      }
    } else {
      console.log(`UNSPLASH GET PHOTOS STATUS: FETCH FAILED. Status code: ${result.status}, Text: ${result.statusText}..`)
    }
  } catch (error) {
    console.log('UNSPLASH GET PHOTOS STATUS: ERROR CATCHED. LOGGING ERROR...')
    console.log(error)

  } finally {
    if (success) {
      return result_json
    } else return null
  }
}

export class WallpapersComponent extends Component {

  constructor(props) {
    super(props);
    this.state={
      photos: null,
      isPhotosLoading: false,
      isHeaderShown: true,
    };
    this.getUnsplashAsync = this.getUnsplashAsync.bind(this);
    this.HeaderText = this.HeaderText.bind(this);
    this.WallpaperListHeader = this.WallpaperListHeader.bind(this);
    this.renderItem = this.renderItem.bind(this);
    this.PhotoList = this.PhotoList.bind(this); 
  }

  componentDidMount() {
    console.log('"Wallpapers" component did mount...')
    this.getUnsplashAsync()
  }

  async getUnsplashAsync() {
    this.setState({isPhotosLoading: true})
    let route = this.props.route
    if (this.props.route.name=='WallpaperTopics') {
      console.log('WALLPAPER COMPONENT TYPE: WallpaperTopics')
      this.setState({photos: route.params.photoList}) 

    } else {
      let unsplashParams = {
        endpoint: `topics/${route.params.selectedImage.id}/photos`,
        page: 1, per_page: 30, order_by: 'popular', orientation: 'portrait'
      }
      let asyncResult = await getUnsplash(unsplashParams)
      this.setState({photos: asyncResult, isPhotosLoading: false}) 
    }
    this.setState({isPhotosLoading: false}) 
  }  

  HeaderText () {
    const topicsScreen = this.props.route.name=='WallpaperTopics'
    return(
      this.state.isPhotosLoading ? (
        <ActivityIndicator size='large' color='black' />
      ) : (
        this.state.photos ? (
          <>
            { !topicsScreen ? <Text style={[styles.h1]}>{ formatTopicName(this.props.route.params.selectedImage.slug) }</Text> : null }
            <Text style={[styles.headerLabel, {fontSize: topicsScreen ? 24 : 20 }]}>{ this.props.route.params.headerText }</Text>
          </>
        ) : (
          <Text style={styles.headerLabel}>Erro ao carregar imagems...</Text>
        )
      )
    )
  }
  
  WallpaperListHeader () {
    const hide = !this.state.isHeaderShown
    return(
      <Pressable
      style={[styles.header, { height: hide ? 0 : styles.header.height }]}
      onPress={() => {this.setState({ isHeaderShown: false }); console.log('close button clicked...')}}
      >
        <this.HeaderText />
      </Pressable>
    )
  }

  renderItem = ({ item }) => (
    <Item item={item} {...this.props} />
  );

  PhotoList = () => (
    <FlatList
      data={this.state.photos}
      renderItem={this.renderItem}
      keyExtractor={item => item.id}
      numColumns={2}
      />
  )

  render() {
    return (
        <SafeAreaView style={styles.photosView}>
          <this.PhotoList />
          <this.WallpaperListHeader />
        </SafeAreaView>
    );  
  }

}